@-webkit-keyframes spin {
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes ping {
  75%, 100% {
    -webkit-transform: scale(2);
            transform: scale(2);
    opacity: 0;
  }
}

@keyframes ping {
  75%, 100% {
    -webkit-transform: scale(2);
            transform: scale(2);
    opacity: 0;
  }
}

@-webkit-keyframes pulse {
  50% {
    opacity: .5;
  }
}

@keyframes pulse {
  50% {
    opacity: .5;
  }
}

@-webkit-keyframes bounce {
  0%, 100% {
    -webkit-transform: translateY(-25%);
            transform: translateY(-25%);
    -webkit-animation-timing-function: cubic-bezier(0.8,0,1,1);
            animation-timing-function: cubic-bezier(0.8,0,1,1);
  }

  50% {
    -webkit-transform: none;
            transform: none;
    -webkit-animation-timing-function: cubic-bezier(0,0,0.2,1);
            animation-timing-function: cubic-bezier(0,0,0.2,1);
  }
}

@keyframes bounce {
  0%, 100% {
    -webkit-transform: translateY(-25%);
            transform: translateY(-25%);
    -webkit-animation-timing-function: cubic-bezier(0.8,0,1,1);
            animation-timing-function: cubic-bezier(0.8,0,1,1);
  }

  50% {
    -webkit-transform: none;
            transform: none;
    -webkit-animation-timing-function: cubic-bezier(0,0,0.2,1);
            animation-timing-function: cubic-bezier(0,0,0.2,1);
  }
}

@import url('https://fonts.googleapis.com/css?family=Oleo+Script&display=swap');

@import url('https://fonts.googleapis.com/css?family=Open+Sans&display=swap');

:root {
  --tweries-black-text: #050505;
  --tweries-blue: #1da1f2;
  --tweries-blue-button: #8ccef7;
  --tweries-blue-white: #f8f8ff;
  --tweries-dark-blue: #0073bf;
  --tweries-dark-blue-button: rgba(0, 115, 191, 0.7);
  --tweries-leckerli-one: 'Leckerli One', cursive;
  --tweries-off-white: #fbfbfb;
}

body {
  color: var(--tweries-black-text);
  font-family: 'Open Sans', sans-serif;
}

.tweries-background-color-blue-button {
  background-color: var(--tweries-blue-button);
}

.tweries-background-color-blue-button:hover {
  background-color: var(--tweries-dark-blue-button);
  color: var(--tweries-off-white);
}

.tweries-background-color-blue-white {
  background-color: var(--tweries-blue-white);
}

.tweries-background-color-off-white {
  background-color: var(--tweries-off-white);
}

.tweries-border {
  border: 1px solid #bdbdbd;
}

.tweries-border-color {
  border-color: var(--tweries-dark-blue);
}

.tweries-color-blue {
  color: var(--tweries-blue);
}

.tweries-color-dark-blue {
  color: var(--tweries-dark-blue);
}

.tweries-font-family {
  font-family: 'Oleo Script', cursive;
}

/* tooltip */

.tooltip + .tooltip-text {
  position: absolute;
  text-align: center;
  visibility: hidden;
  z-index: 100;
}

.tooltip:hover + .tooltip-text {
  background-color: var(--tweries-blue-white);
  visibility: visible;
}
